const Component = require('./CCComponent');
const misc = require('../utils/misc');

/**
 * !#en Enum for transition type.
 * !#zh 
 * @enum Button.Transition
 */
let Transition = cc.Enum({
    /**
     * !#en The none type.
     * !#zh 
     * @property {Number} NONE
     */
    NONE: 0,

    /**
     * !#en The color type.
     * !#zh 
     * @property {Number} COLOR
     */
    COLOR: 1,

    /**
     * !#en The sprite type.
     * !#zh 
     * @property {Number} SPRITE
     */
    SPRITE: 2,
    /**
     * !#en The scale type
     * !#zh 
     * @property {Number} SCALE
     */
    SCALE: 3
});

/**
 * !#en
 * Button has 4 Transition types<br/>
 * When Button state changed:<br/>
 *  If Transition type is Button.Transition.NONE, Button will do nothing<br/>
 *  If Transition type is Button.Transition.COLOR, Button will change target's color<br/>
 *  If Transition type is Button.Transition.SPRITE, Button will change target Sprite's sprite<br/>
 *  If Transition type is Button.Transition.SCALE, Button will change target node's scale<br/>
 *
 * Button will trigger 5 events:<br/>
 *  Button.EVENT_TOUCH_DOWN<br/>
 *  Button.EVENT_TOUCH_UP<br/>
 *  Button.EVENT_HOVER_IN<br/>
 *  Button.EVENT_HOVER_MOVE<br/>
 *  Button.EVENT_HOVER_OUT<br/>
 *
 * !#zh
 * ,<br/>
 *
 *  Transition <br/>
 *   -Button.Transition.NONE   // <br/>
 *   -Button.Transition.COLOR  // <br/>
 *   -Button.Transition.SPRITE // <br/>
 *   -Button.Transition.SCALE // <br/>
 *
 *  Node <br/>
 *   // <br/>
 *   -cc.Node.EventType.TOUCH_START  // <br/>
 *   -cc.Node.EventType.TOUCH_Move   // <br/>
 *   -cc.Node.EventType.TOUCH_END    // <br/>
 *   -cc.Node.EventType.TOUCH_CANCEL // <br/>
 *   //  PC <br/>
 *   -cc.Node.EventType.MOUSE_DOWN  // <br/>
 *   -cc.Node.EventType.MOUSE_MOVE  // <br/>
 *   -cc.Node.EventType.MOUSE_ENTER // <br/>
 *   -cc.Node.EventType.MOUSE_LEAVE // <br/>
 *   -cc.Node.EventType.MOUSE_UP    // <br/>
 *   -cc.Node.EventType.MOUSE_WHEEL // <br/>
 *
 * @class Button
 * @extends Component
 * @example
 *
 * // Add an event to the button.
 * button.node.on(cc.Node.EventType.TOUCH_START, function (event) {
 *     cc.log("This is a callback after the trigger event");
 * });

 * // You could also add a click event
 * //Note: In this way, you can't get the touch event info, so use it wisely.
 * button.node.on('click', function (button) {
 *    //The event is a custom event, you could get the Button component via first argument
 * })
 *
 */
let Button = cc.Class({
    name: 'cc.Button',
    extends: Component,

    ctor () {
        this._pressed = false;
        this._hovered = false;
        this._fromColor = null;
        this._toColor = null;
        this._time = 0;
        this._transitionFinished = true;
        this._fromScale = 1.0;
        this._toScale = 1.0;
        this._originalScale = 1.0;

        this._sprite = null;

        if (CC_EDITOR) {
            this._previousNormalSprite = null;
        }
    },

    editor: CC_EDITOR && {
        menu: 'i18n:MAIN_MENU.component.ui/Button',
        help: 'i18n:COMPONENT.help_url.button',
        inspector: 'packages://inspector/inspectors/comps/button.js',
        executeInEditMode: true
    },

    properties: {
        /**
         * !#en
         * Whether the Button is disabled.
         * If true, the Button will trigger event and do transition.
         * !#zh
         *  false
         * @property {Boolean} interactable
         * @default true
         */
        interactable: {
            default: true,
            tooltip: CC_DEV && 'i18n:COMPONENT.button.interactable',
            notify (oldValue) {
                if (CC_EDITOR) {
                    if (oldValue) {
                        this._previousNormalSprite = this.normalSprite;
                    } else {
                        this.normalSprite = this._previousNormalSprite;
                    }
                }
                this._updateState();

                if (!this.interactable) {
                    this._resetState();
                }
            },
            animatable: false
        },

        _resizeToTarget: {
            animatable: false,
            set (value) {
                if (value) {
                    this._resizeNodeToTargetNode();
                }
            }
        },

        /**
         * !#en When this flag is true, Button target sprite will turn gray when interactable is false.
         * !#zh  true button  interactable  false  shader  button  target  sprite 
         * @property {Boolean} enableAutoGrayEffect
         */
        enableAutoGrayEffect: {
            default: false,
            tooltip: CC_DEV && 'i18n:COMPONENT.button.auto_gray_effect',
            notify () {
                this._updateDisabledState();
            }
        },

        /**
         * !#en Transition type
         * !#zh 
         * @property {Button.Transition} transition
         * @default Button.Transition.Node
         */
        transition: {
            default: Transition.NONE,
            tooltip: CC_DEV && 'i18n:COMPONENT.button.transition',
            type: Transition,
            animatable: false
        },

        // color transition

        /**
         * !#en Normal state color.
         * !#zh 
         * @property {Color} normalColor
         */
        normalColor: {
            default: cc.color(214, 214, 214),
            displayName: 'Normal',
            tooltip: CC_DEV && 'i18n:COMPONENT.button.normal_color',
            notify () {
                this._updateState();
            }
        },

        /**
         * !#en Pressed state color
         * !#zh 
         * @property {Color} pressedColor
         */
        pressedColor: {
            default: cc.color(211, 211, 211),
            displayName: 'Pressed',
            tooltip: CC_DEV && 'i18n:COMPONENT.button.pressed_color',
        },

        /**
         * !#en Hover state color
         * !#zh 
         * @property {Color} hoverColor
         */
        hoverColor: {
            default: cc.Color.WHITE,
            displayName: 'Hover',
            tooltip: CC_DEV && 'i18n:COMPONENT.button.hover_color',
        },

        /**
         * !#en Disabled state color
         * !#zh 
         * @property {Color} disabledColor
         */
        disabledColor: {
            default: cc.color(124, 124, 124),
            displayName: 'Disabled',
            tooltip: CC_DEV && 'i18n:COMPONENT.button.disabled_color',
            notify () {
                this._updateState();
            }
        },

        /**
         * !#en Color and Scale transition duration
         * !#zh 
         * @property {Number} duration
         */
        duration: {
            default: 0.1,
            range: [0, 10],
            tooltip: CC_DEV && 'i18n:COMPONENT.button.duration',
        },

        /**
         * !#en  When user press the button, the button will zoom to a scale.
         * The final scale of the button  equals (button original scale * zoomScale)
         * !#zh  Button  scale * zoomScale
         * @property {Number} zoomScale
         */
        zoomScale: {
            default: 1.2,
            tooltip: CC_DEV && 'i18n:COMPONENT.button.zoom_scale'
        },

        // sprite transition
        /**
         * !#en Normal state sprite
         * !#zh  Sprite 
         * @property {SpriteFrame} normalSprite
         */
        normalSprite: {
            default: null,
            type: cc.SpriteFrame,
            displayName: 'Normal',
            tooltip: CC_DEV && 'i18n:COMPONENT.button.normal_sprite',
            notify () {
                this._updateState();
            }
        },

        /**
         * !#en Pressed state sprite
         * !#zh  Sprite 
         * @property {SpriteFrame} pressedSprite
         */
        pressedSprite: {
            default: null,
            type: cc.SpriteFrame,
            displayName: 'Pressed',
            tooltip: CC_DEV && 'i18n:COMPONENT.button.pressed_sprite',
            formerlySerializedAs: 'pressedSprite',
            notify () {
                this._updateState();
            }
        },

        /**
         * !#en Hover state sprite
         * !#zh  Sprite 
         * @property {SpriteFrame} hoverSprite
         */
        hoverSprite: {
            default: null,
            type: cc.SpriteFrame,
            displayName: 'Hover',
            tooltip: CC_DEV && 'i18n:COMPONENT.button.hover_sprite',
            formerlySerializedAs: 'hoverSprite',
            notify () {
                this._updateState();
            }
        },

        /**
         * !#en Disabled state sprite
         * !#zh  Sprite 
         * @property {SpriteFrame} disabledSprite
         */
        disabledSprite: {
            default: null,
            type: cc.SpriteFrame,
            displayName: 'Disabled',
            tooltip: CC_DEV && 'i18n:COMPONENT.button.disabled_sprite',
            notify () {
                this._updateState();
            }
        },

        /**
         * !#en
         * Transition target.
         * When Button state changed:
         *  If Transition type is Button.Transition.NONE, Button will do nothing
         *  If Transition type is Button.Transition.COLOR, Button will change target's color
         *  If Transition type is Button.Transition.SPRITE, Button will change target Sprite's sprite
         * !#zh
         * 
         * 
         * - Transition type  Button.Transition.NONE
         * - Transition type  Button.Transition.COLOR
         * - Transition type  Button.Transition.Sprite Sprite  Sprite 
         * @property {Node} target
         */
        target: {
            default: null,
            type: cc.Node,
            tooltip: CC_DEV && "i18n:COMPONENT.button.target",
            notify () {
                this._applyTarget();
            }
        },

        /**
         * !#en If Button is clicked, it will trigger event's handler
         * !#zh 
         * @property {Component.EventHandler[]} clickEvents
         */
        clickEvents: {
            default: [],
            type: cc.Component.EventHandler,
            tooltip: CC_DEV && 'i18n:COMPONENT.button.click_events',
        }
    },

    statics: {
        Transition: Transition,
    },

    __preload () {
        if (!this.target) {
            this.target = this.node;
        }
        this._applyTarget();
        this._updateState();
    },

    _resetState () {
        this._pressed = false;
        this._hovered = false;
        // // Restore button status
        let target = this.target;
        let transition = this.transition;
        if (transition === Transition.COLOR) {
            target.color = this.normalColor;
        } else if (transition === Transition.SCALE) {
            target.scale = this._originalScale;
        }
        this._transitionFinished = true;
    },

    onEnable () {
        // check sprite frames
        if (this.normalSprite) {
            this.normalSprite.ensureLoadTexture();
        }
        if (this.hoverSprite) {
            this.hoverSprite.ensureLoadTexture();
        }
        if (this.pressedSprite) {
            this.pressedSprite.ensureLoadTexture();
        }
        if (this.disabledSprite) {
            this.disabledSprite.ensureLoadTexture();
        }
        //
        if (!CC_EDITOR) {
            this._registerEvent();
        } else {
            this.node.on('spriteframe-changed', function (comp) {
                if (this.transition === Transition.SPRITE) {
                    this.normalSprite = comp.spriteFrame;
                }
            }.bind(this));
        }
    },

    onDisable () {
        this._resetState();

        if (!CC_EDITOR) {
            this.node.off(cc.Node.EventType.TOUCH_START, this._onTouchBegan, this);
            this.node.off(cc.Node.EventType.TOUCH_MOVE, this._onTouchMove, this);
            this.node.off(cc.Node.EventType.TOUCH_END, this._onTouchEnded, this);
            this.node.off(cc.Node.EventType.TOUCH_CANCEL, this._onTouchCancel, this);

            this.node.off(cc.Node.EventType.MOUSE_ENTER, this._onMouseMoveIn, this);
            this.node.off(cc.Node.EventType.MOUSE_LEAVE, this._onMouseMoveOut, this);
        } else {
            this.node.off('spriteframe-changed');
        }
    },

    update (dt) {
        let target = this.target;
        if (this._transitionFinished) return;
        if (this.transition !== Transition.COLOR && this.transition !== Transition.SCALE) return;

        this.time += dt;
        let ratio = 1.0;
        if (this.duration > 0) {
            ratio = this.time / this.duration;
        }

        if (ratio >= 1) {
            ratio = 1;
            this._transitionFinished = true;
        }

        if (this.transition === Transition.COLOR) {
            target.color = this._fromColor.lerp(this._toColor, ratio);
        } else if (this.transition === Transition.SCALE) {
            target.scale = misc.lerp(this._fromScale, this._toScale, ratio);
        }

    },

    _registerEvent () {
        this.node.on(cc.Node.EventType.TOUCH_START, this._onTouchBegan, this);
        this.node.on(cc.Node.EventType.TOUCH_MOVE, this._onTouchMove, this);
        this.node.on(cc.Node.EventType.TOUCH_END, this._onTouchEnded, this);
        this.node.on(cc.Node.EventType.TOUCH_CANCEL, this._onTouchCancel, this);

        this.node.on(cc.Node.EventType.MOUSE_ENTER, this._onMouseMoveIn, this);
        this.node.on(cc.Node.EventType.MOUSE_LEAVE, this._onMouseMoveOut, this);
    },

    _getTargetSprite (target) {
        let sprite = null;
        if (target) {
            sprite = target.getComponent(cc.Sprite);
        }
        return sprite;
    },

    _applyTarget () {
        this._sprite = this._getTargetSprite(this.target);
        if (this.target) {
            this._originalScale = this.target.scale;
        }
    },

    // touch event handler
    _onTouchBegan (event) {
        if (!this.interactable || !this.enabledInHierarchy) return;

        this._pressed = true;
        this._updateState();
        event.stopPropagation();
    },

    _onTouchMove (event) {
        if (!this.interactable || !this.enabledInHierarchy || !this._pressed) return;
        // mobile phone will not emit _onMouseMoveOut,
        // so we have to do hit test when touch moving
        let touch = event.touch;
        let hit = this.node._hitTest(touch.getLocation());

        if (this.transition === Transition.SCALE && this.target) {
            if (hit) {
                this._fromScale = this._originalScale;
                this._toScale = this._originalScale * this.zoomScale;
                this._transitionFinished = false;
            } else {
                this.time = 0;
                this._transitionFinished = true;
                this.target.scale = this._originalScale;
            }
        } else {
            let state;
            if (hit) {
                state = 'pressed';
            } else {
                state = 'normal';
            }
            this._applyTransition(state);
        }
        event.stopPropagation();
    },

    _onTouchEnded (event) {
        if (!this.interactable || !this.enabledInHierarchy) return;

        if (this._pressed) {
            cc.Component.EventHandler.emitEvents(this.clickEvents, event);
            this.node.emit('click', this);
        }
        this._pressed = false;
        this._updateState();
        event.stopPropagation();
    },

    _onTouchCancel () {
        if (!this.interactable || !this.enabledInHierarchy) return;

        this._pressed = false;
        this._updateState();
    },

    _onMouseMoveIn () {
        if (this._pressed || !this.interactable || !this.enabledInHierarchy) return;
        if (this.transition === Transition.SPRITE && !this.hoverSprite) return;

        if (!this._hovered) {
            this._hovered = true;
            this._updateState();
        }
    },

    _onMouseMoveOut () {
        if (this._hovered) {
            this._hovered = false;
            this._updateState();
        }
    },

    // state handler
    _updateState () {
        let state = this._getButtonState();
        this._applyTransition(state);
        this._updateDisabledState();
    },

    _getButtonState () {
        let state;
        if (!this.interactable) {
            state = 'disabled';
        }
        else if (this._pressed) {
            state = 'pressed';
        }
        else if (this._hovered) {
            state = 'hover';
        }
        else {
            state = 'normal';
        }
        return state;
    },

    _updateColorTransition (state) {
        let color = this[state + 'Color'];
        let target = this.target;

        if (CC_EDITOR) {
            target.color = color;
        }
        else {
            this._fromColor = target.color.clone();
            this._toColor = color;
            this.time = 0;
            this._transitionFinished = false;
        }
    },

    _updateSpriteTransition (state) {
        let sprite = this[state + 'Sprite'];
        if (this._sprite && sprite) {
            this._sprite.spriteFrame = sprite;
        }
    },

    _updateScaleTransition (state) {
        if (state === 'pressed') {
            this._zoomUp();
        } else {
            this._zoomBack();
        }
    },

    _zoomUp () {
        this._fromScale = this._originalScale;
        this._toScale = this._originalScale * this.zoomScale;
        this.time = 0;
        this._transitionFinished = false;
    },

    _zoomBack () {
        this._fromScale = this.target.scale;
        this._toScale = this._originalScale;
        this.time = 0;
        this._transitionFinished = false;
    },

    _applyTransition (state) {
        let transition = this.transition;
        if (transition === Transition.COLOR) {
            this._updateColorTransition(state);
        } else if (transition === Transition.SPRITE) {
            this._updateSpriteTransition(state);
        } else if (transition === Transition.SCALE) {
            this._updateScaleTransition(state);
        }
    },

    _resizeNodeToTargetNode: CC_EDITOR && function () {
        if (this.target) {
            this.node.setContentSize(this.target.getContentSize());
        }
    },

    _updateDisabledState () {
        if (this._sprite) {
            this._sprite.setState(cc.Sprite.State.NORMAL);
        }
        if (this.enableAutoGrayEffect && this.transition !== Transition.COLOR) {
            if (!(this.transition === Transition.SPRITE && this.disabledSprite)) {
                if (this._sprite && !this.interactable) {
                    this._sprite.setState(cc.Sprite.State.GRAY);
                }
            }
        }
    }
});

cc.Button = module.exports = Button;

/**
 * !#en
 * Note: This event is emitted from the node to which the component belongs.
 * !#zh
 *  Node  node.on 
 * @event click
 * @param {Event.EventCustom} event
 * @param {Button} button - The Button component.
 */
